<template>
  <div class="productDetail">
    <el-form :model="form" :rules="rules" ref="form" size="small" :label-width="formLabelWidth">
      <el-form-item label="产品分类" prop="productClass">
        <el-select :clearable="true" v-model.trim="form.productClass" placeholder="请选择产品分类" class="selectWidth">
          <el-option label="为你推荐" value="1"></el-option>
          <el-option label="鲜花系列" value="2"></el-option>
          <el-option label="干花系列" value="3"></el-option>
          <el-option label="礼品系列" value="4"></el-option>
          <el-option label="绿植多肉" value="5"></el-option>
          <el-option label="花器耗材" value="6"></el-option>
          <el-option label="专属定制" value="7"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="产品名称" prop="productName">
        <el-input :clearable="true" v-model="form.productName" placeholder="请输入产品名称" class="inputWidth"></el-input>
      </el-form-item>
      <el-form-item label="产品品牌" prop="productBrand">
        <el-select v-model.trim="form.productBrand" placeholder="请选择产品品牌" clearable class="selectWidth">
          <el-option label="HTF_HZH" value="1"></el-option>
          <el-option label="NIUBINHJ" value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="产品描述" prop="productDescribe">
        <el-input :clearable="true" v-model.trim="form.productDescribe" placeholder="请输入产品描述" class="inputWidth"></el-input>
      </el-form-item>
      <el-form-item label="封面图" prop="coverMap" ref="coverMap" class="coverMap">
        <el-upload
          action="https://jsonplaceholder.typicode.com/posts/"
          accept="image/png, image/jpeg, image/jpg"
          list-type="picture-card"
          :limit= 1
          :on-exceed="exceedFuc"
          :on-preview="coverMapPictureCardPreview"
          :on-success="coverMapSuccess"
          :on-error="coverMapError"
          :before-upload="coverMapBeforeUpload"
          :on-remove="coverMapRemove">
          <i class="el-icon-plus"></i>
          <div slot="tip" class="el-upload__tip">请上传png、jpg及jpeg格式图片(大小500KB以下)</div>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible" :show-close="false" style="padding:0">
          <img width="100%" :src="form.coverMap" class="coverMap">
        </el-dialog>
      </el-form-item>
      <el-form-item label="产品视图" prop="productPictures" ref="productPictures" class="productPictures" style="width: 71%">
        <el-upload
          action="https://jsonplaceholder.typicode.com/posts/"
          list-type="picture-card"
          :limit= 8
          accept="image/png, image/jpeg, image/jpg, video/mp4"
          :on-exceed="productPictureseExceedFuc"
          :on-preview="productPicturesPreview"
          :on-success="productPicturesSuccess"
          :on-error="productPicturesError"
          :before-upload="productPicturesUpload"
          :on-remove="productPicturesRemove">
          <i class="el-icon-plus"></i>
          <div slot="tip" class="el-upload__tip">请上传png、jpg及jpeg格式图片(大小500KB以下)&nbsp;&nbsp;&nbsp;请上传mp4文件(大小2MB以下)</div>
        </el-upload>
        <el-dialog :visible.sync="productPicturesVisible" :show-close="false">
          <video v-if="videoFlag" width="100%" :src="form.productPictures" controls="controls">您的浏览器不支持视频播放</video>
          <img v-else width="100%" :src="form.productPictures" class="productPictures">
        </el-dialog>
      </el-form-item>
      <el-form-item label="原价" prop="originalPrice">
        <el-input :clearable="true" v-model.trim="form.originalPrice" placeholder="请输入原价" class="inputWidth"></el-input>&nbsp;&nbsp;元
      </el-form-item>
      <el-form-item label="折扣值" prop="discount">
        <el-input :clearable="true" v-model.trim="form.discount" placeholder="请输入折扣值" class="inputWidth"></el-input>&nbsp;&nbsp;%
      </el-form-item>
      <el-form-item label="现价">
        <el-input :clearable="true" :disabled="true" v-model.trim="presentPrice" class="inputWidth"></el-input>&nbsp;&nbsp;元
      </el-form-item>
      <el-form-item label="会员专享价" prop="exclusivePrice">
        <el-input :clearable="true" v-model.trim="form.exclusivePrice" placeholder="请输入会员专享价" class="inputWidth"></el-input>&nbsp;&nbsp;元
      </el-form-item>
      <el-form-item label="库存数量" prop="projectStock">
        <el-input :clearable="true" v-model.trim="form.projectStock" placeholder="请输入库存数量" class="inputWidth"></el-input>
      </el-form-item>
      <el-form-item label="特别提醒" prop="remarks">
        <el-input
          class="textarea"
          :clearable="true"
          v-model.trim="form.remarks"
          resize="none"
          type="textarea"
          :rows="3"
          placeholder="请输入特别提醒"></el-input>
      </el-form-item>
      <el-form-item label="推荐理由" prop="recommendationReason" ref="recommendationReason">
        <quillEditor ref="quillEditor" placeholder="请输入推荐理由"/>
      </el-form-item>
      <el-form-item label="规格参数" prop="specificationParameter" ref="specificationParameter">
        <quillEditor ref="quillEditor" placeholder="请输入规格参数"/>
      </el-form-item>
      <el-form-item label="保养说明" prop="maintenanceInstructions" ref="maintenanceInstructions">
        <quillEditor ref="quillEditor" placeholder="请输入保养说明"/>
      </el-form-item>
      <el-form-item label="运输说明" prop="transportRemarks">
        <el-input
          class="textarea"
          :clearable="true"
          v-model.trim="form.transportRemarks"
          resize="none"
          type="textarea"
          :rows="3"
          placeholder="请输入运输说明"></el-input>
      </el-form-item>
      <el-form-item label="退换货说明" prop="retreatRemarks">
        <el-input
          class="textarea"
          :clearable="true"
          v-model.trim="form.retreatRemarks"
          resize="none"
          type="textarea"
          :rows="3"
          placeholder="请输入退换货说明"></el-input>
      </el-form-item>
      <el-form-item label="运费" prop="freight">
        <el-input :clearable="true" v-model.trim="form.freight" placeholder="请输入运费" class="inputWidth"></el-input>&nbsp;&nbsp;元
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="closeForm">取 消</el-button>
      <el-button size="small" type="primary" @click="submitForm('form')">确 定</el-button>
    </div>
    <!-- 手机展示 -->
    <div class="iphoneContainer">
      <div class="iphone">
        <div class="iphoneTop"></div>
        <div class="content"></div>
      </div>
    </div>
  </div>
</template>
<script>
import quillEditor from '../../components/QuillEditor'
export default {
  name: 'ProductDetail',
  components: {
    quillEditor
  },
  data () {
    return {
      videoFlag: false,
      productPicturesVisible: false,
      dialogVisible: false,
      form: {
        productClass: '',
        productName: '',
        productBrand: '',
        productDescribe: '',
        coverMap: '',
        productPictures: '',
        recommendationReason: '',
        specificationParameter: '',
        maintenanceInstructions: '',
        originalPrice: '',
        discount: '',
        exclusivePrice: '',
        projectStock: '',
        remarks: '',
        transportRemarks: '',
        retreatRemarks: '',
        freight: 0
      },
      formLabelWidth: '90px',
      rules: {
        productClass: [
          { required: true, message: '请选择产品分类', trigger: 'change' }
        ],
        productName: [
          { required: true, message: '请输入产品名称', trigger: 'blur' }
        ],
        productBrand: [
          { required: true, message: '请选择产品品牌', trigger: 'change' }
        ],
        productDescribe: [
          { required: true, message: '请输入产品描述', trigger: 'blur' }
        ],
        coverMap: [
          { required: true, message: '请上传封面图', trigger: 'change' }
        ],
        productPictures: [
          { required: true, message: '请上传产品图片或视频', trigger: 'change' }
        ],
        originalPrice: [
          { required: true, message: '请输入产品原价', trigger: 'blur' }
        ],
        projectStock: [
          { required: true, message: '请输入产品库存数量', trigger: 'blur' }
        ],
        freight: [
          { required: true, message: '请输入产品运费', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    presentPrice () {
      return this.form.originalPrice * ((this.form.discount / 100) ? (this.form.discount / 100) : 1)
    }
  },
  created () {
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.closeForm()
        } else {
          return false
        }
      })
    },
    // 关闭dialog
    closeForm () {
      this.$refs['form'].resetFields()
      let tagsData = JSON.parse(sessionStorage.getItem('tags'))
      let index = tagsData.findIndex((item, i) => {
        return item.path === '/addProduct'
      })
      tagsData.splice(index, 1)
      sessionStorage.setItem('tags', JSON.stringify(tagsData))
      this.$router.push('/productUpperMg')
    },
    // 封面图
    coverMapBeforeUpload (file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/jpg'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isJPG) {
        this.$message.error('上传产品图片只能是 jpg/jpeg/png 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传产品图片大小不能超过 500KB!')
      }
      return isJPG && isLt2M
    },
    coverMapRemove (file, fileList) {
      if (!fileList.length) {
        this.form.coverMap = ''
      }
    },
    coverMapPictureCardPreview (file) {
      this.form.coverMap = file.url
      this.dialogVisible = true
      this.$refs.coverMap.clearValidate()
    },
    exceedFuc () {
      this.$message.error('产品封面图只允许上传一张!')
    },
    coverMapSuccess (res, file) {
      this.form.coverMap = URL.createObjectURL(file.raw)
      this.$refs.coverMap.clearValidate()
    },
    coverMapError () {
      this.$message.error('产品封面上传失败!')
    },
    // 产品图
    productPicturesUpload (file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/jpg' || file.type === 'video/mp4'
      let isLt2M = null
      if (file.type === 'video/mp4') {
        isLt2M = file.size / 1024 / 1024 < 2
      } else {
        isLt2M = file.size / 1024 < 500
      }
      if (!isJPG) {
        this.$message.error('上传产品图片只能是 jpg/jpeg/png 格式! 视频只能是MP4格式!')
      }
      if (file.type === 'video/mp4' && !isLt2M) {
        this.$message.error('上传产品视频大小不能超过 2MB!')
      } else if (!isLt2M) {
        this.$message.error('上传产品图片大小不能超过 500KB!')
      }
      return isJPG && isLt2M
    },
    productPicturesRemove (file, fileList) {
      if (!fileList.length) {
        this.form.productPictures = ''
      }
    },
    productPicturesPreview (file) {
      this.form.productPictures = file.url
      this.videoFlag = file.raw.type === 'video/mp4'
      this.productPicturesVisible = true
      this.$refs.productPictures.clearValidate()
    },
    productPicturesSuccess (res, file) {
      this.form.productPictures = URL.createObjectURL(file.raw)
      this.$refs.productPictures.clearValidate()
    },
    productPicturesError (res, file) {
      if (file.raw.type === 'video/mp4') {
        this.$message.error('产品视频上传失败!')
      } else {
        this.$message.error('产品图片上传失败!')
      }
    },
    productPictureseExceedFuc () {
      this.$message.error('产品视图最多只允许上传8张!')
    }
  }
}
</script>
<style lang='stylus' ref='stylesheet/stylus'>
.productDetail
  display inline-block
  width 100%
  .selectWidth
    width 25%
  .inputWidth
    width 50%
  .textarea
    width 100%
  .el-dialog__header
    display none
  .el-dialog__body
    display flex
    align-items center
    justify-content center
    padding 20px
  .dialog-footer
    text-align right
  // 手机展示
  .iphoneContainer
    .iphone
      position absolute
      width 375px
      height 850px
      top 18px
      right 18px
      background url('../../../static/img/iphone.png') no-repeat top right
      background-size 100% auto
</style>



